body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #017abf; 
}
.parent-container{
  
  padding: 30px 0px 10px 10px;
}
.task-containers-wrapper{
  flex: 1 1;
  display: flex;
  overflow: auto;
}
.scrollable-wrap{
  min-height: -webkit-min-content;
  display: flex;
}
.add-container-btn{
  align-self: flex-end;
  border-radius: 20px;
  height: 32px;
  width: 30px;
  font-size: 20px;
  margin: 0px 0 10px 0px;
  border: none;
  background-color: white;
}

.add-container-btn:focus{
  outline:0;
}
.add-container-btn:hover{
  background-color: #e7ecee;
  cursor: pointer;
}
.tasks-container-with-modal{
  width: 300px;
  margin-right: 15px;
  
}
.tasks-container{
  width: 100%;
  padding: 0px 0px;
  border-radius: 5px;
  border: 1px solid darkgray;
  background-color: lightgray
}
.add-task-btn{
  width: 100%;
  font-size: 16px;
  border: none;
  background-color: dodgerblue;
  border-radius: 5px;
}
.add-task-btn:focus{
  outline: 0;
}
.add-task-btn:hover{
  background-color: rgb(88, 171, 255);
  cursor: pointer;
}
.tasks-container-title{
  text-align: center;
  background-color: rgb(233, 233, 233);
  font-size: 22px;
  font-weight: bold;
  border-radius: 5px;
}
.task-list{
  padding: 0px 5px 5px 5px;
}

@media(max-width: 768px){
  .tasks-container-with-modal{
    width: 200px;
    margin-right: 7px;
  }
  .tasks-container-title{
    font-size: 16px;
  }
  .add-task-btn{
    font-size: 12px;
  }
}
@media(max-width: 425px){
  .tasks-container-with-modal{
    width: 100px;
    margin-right: 5px;
    
  }
}
.task{
  padding: 5px 5px;
  margin: 0 0 5px 0;
  background: white;
  border-radius: 5px;
}
.navigation-bar{
  background-color: #006097;
  padding-bottom: 5px;
}
.navigation-list{
  display: flex;
  list-style-type: none;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
}
img{
  height: 35px;
  width: 120px;
  object-fit: cover;
  padding-right: 20px;
}
.navigation-list li{
  color: white;
  align-self: center;
  font-weight: bold;
  padding: 0px 10px 0px 10px;
}
@media(max-width:425px){
  .navigation-list{
    font-size: 10px;
  }
}
